import {WinJSDec, fnError} from './APP/config';

var name = 'DEVICE_SHAKE';
var DEVICE_SHAKE = (fn = _ => {
}) => {
  if (typeof fn === 'function') {
    WinJSDec(_ => {
      window.WinJSBridge.call('device', 'enableshake', {enable: true}, function (res) {
      });
      document.addEventListener('ondeviceshake', fn);
    });
  } else {
    throw new TypeError(`[${name}] ${fnError}`);
  }
};
export default DEVICE_SHAKE;